import React, { Component } from 'react'
import { projectData } from '../utils/constants';


export default class Projects extends Component {
	render() {
		return (
			<div>
				<section className="colorlib-work" data-section="projects">
					<div className="colorlib-narrow-content">
						<div className="row">
							<div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
								<span className="heading-meta">My Work</span>
								<h2 className="colorlib-heading animate-box">Projects</h2>
							</div>
						</div>
						<div className="row scrolling-wrapper-flexbox">
							{
									projectData && projectData.map((data) => (
                                        <div className="col-md-4 col-sm-6 animate-box" data-animate-effect={data.animation}>
                                            <div className="blog-entry">
                                                <a href={data.refUrl} className="blog-img"><img src={data.imageUrl} className="img-responsive" alt="blog" target="_blank"/></a>
                                                <div className="desc">
                                                    <span><small>{data.data}</small> | <small> {data.blogName} </small> | <small> <i className="icon-bubble3" /> 4</small></span>
                                                    <h3><a href={data.refUrl}>{data.name}</a></h3>
                                                    <p>{data.description}</p>
                                                </div>
                                            </div>
                                        </div>
								))
							}
						</div>
					</div>
				</section>
			</div>
		)
	}
}
